/*   ----------------------------------------------------------------------------- GENERAL*/

h1 {
    color: yellow;
}

a:link, a:visited, a:hover, a:active{
    color:#eaeff5;
}

.fluid-container {
    margin: 7%;
}

.color-text-black {
    color: #1d2d44;
}

.color-text-white {
    color: #eaeff5;
}

.background-white {
    background-color: #eaeff5;
    position: relative;
}

.background-white-opacity {
    background-color: rgba(234, 239, 245, 0.03);
}

.heading-main {
    font-size: 2rem;
    font-weight: 700;
    margin-top: 10%;
}

.paragraph-white {
    font-size: 0.8rem;
    font-weight: 200;
    line-height: 1.2;
    margin: 5% 7% 7%;
}
.ornament-box {
    width: 101%;
    margin: 10px 0 0;
}

.box-waves-bottom{
  position: relative;
  bottom: -10px;

}


.icon-back {
    width: 30px;
    height: 30px;
    position: absolute;
    left: 7%;
    top:5%;
}
@media only screen and (min-height: 720px) {
  .box-waves-bottom {
    position:absolute;
    bottom:0;
  }
}

/*   ----------------------------------------------------------------------------- MEMBERS */

.ESNmember-photo-thumbnail {
    border-radius: 100%;
    object-fit: cover;
    margin-right: 20px;
    width: 60px;
    height: 60px;
}

.ESNmember-box {
    padding: 10px 40px;
}

.ESNmember-position{
    font-size: 13px;
}

.hamburger-menu {
    color: #1d2d44;
    position: absolute;
    left: 35px;
    width: 18px;
    top: 50%;
    -ms-transform: translateY(-50%);
    transform: translateY(-50%);
}

.member-container {
    text-align: center;
    padding: 0 5%;
}

.member-data {
    color: #eaeff5;
}

.photo-and-background {
    position: relative;
    width: 200px;
    height: 200px;
    margin: 7% auto 10px;
}

.ESNmember-photo-background {
    width: 200px;
    position: relative;
}

.ESNmember-photo {
    border-radius: 100%;
    object-fit: cover;
    width: 120px;
    height: 120px;
    position: absolute;
    left: 20%;
    top: 20%;
}

.div-box-members {
    position: relative;
}

.box-members {
    padding: 10px;
    font-weight: 400;
}

.member-name {
    font-weight: 600;
    font-size: 2rem;
    margin: 0;
}

.member-position {
    font-size: 1.2rem;
    margin-top: 5px;
    margin-bottom: 30px;
}

.member-contact {
    font-size: 0.8rem;
    margin: 5px auto 15px;
}

.member-contact a{
    color:#eaeff5;
    text-decoration: none;
}

.ESNmember-icon-contact {
    width: 20px;
    height: 20px;
}

.members-label{
  font-size: 1.3rem;
  font-weight: 600;
}


/*   ----------------------------------------------------------------------------- PRACTICAL GUIDE */
/*   ----------------------------------------------------------------------------- OFFICE HOURS */
.dot-icon {
    width: 10px;
    margin: 0 5% 0 5%;
}

.officeHours-days {
    font-size: 1rem;
    margin: 3% 5%;
}

.officeHours-week {
    margin-top:18px;
    margin-bottom:18px;
}

.hour-right{
position: absolute;
right: 10%;
}
/*   ----------------------------------------------------------------------------- EVENTS */

.event-thumbnail {
    border-radius: 10px;
    object-fit: cover;
    width: 100px;
    margin: 8px 10px 8px 0;
}

.event-list-item {
    background-color: #eaeff5;
    --ion-background-color: #eaeff5;
    --ion-text-color: #1d2d44;
}

.event-item-box {
    margin: 10px;
    border-radius: 10px;
}

.event-group-label {
    font-size: 1.5rem;
}

.event-box-small{
  font-size: 0.6rem;
}

.event-box-big{
  font-size: 1rem;

}

.margin-bottom-event{
  margin-bottom: 60px;
}

.event-title{
  font-size: 1.2rem;
  font-weight: 600;
  margin-bottom: 20px;
}

.fluid-container-event-details {
    margin: 0 5% 7%;
    overflow: scroll;
}
.icon-event-description{
  margin-right: 20px;

}

ion-item.event-item-box{
    --inner-highlight-end: 0px;
    --padding-start:8px
}

.small-padding-event{
  margin:0;
}

.member-wrapper{
    overflow:scroll;
}
